import React, {Component} from 'react';
import './App.css';

import Menu from './Menu/Menu.js';

import Contact from './Contact/Contact.js';
import Education from './Education/Education.js';
import Employment from './Employment/Employment.js';
import Portfolio from './Portfolio/Portfolio.js';
import Title from './Title/Title.js';

class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            activePage: 0,
            pages: ["title", "education", "employment", "portfolio", "contact"]
        }

        this.updateActivePage = this.updateActivePage.bind(this);
    }

    numToPageID(n) {
        if (n >= this.state.pages.length) {
            return "err @ numToPageID (" + n + ")";
        }

        return this.state.pages[n];
    }

    pageIDToNum(id) {
        for (var i = 0; i < this.state.pages.length; i++) {
            if (this.state.pages[i] === id) {
                return i;
            }
        }

        console.log("err @ pageIDToNum (" + id + ")");
        return 0;
    }

    loadActivePage() {
        switch (this.state.activePage) {
            case 0:
            default:
                return <Title/>;
            case 1:
                return <Education/>;
            case 2:
                return <Employment/>;
            case 3:
                return <Portfolio/>;
            case 4:
                return <Contact/>;
        }
    }

    updateActivePage(pageID) {
        console.log("updating to " + pageID);
        var n = this.pageIDToNum(pageID);
        this.setState({
            activePage: n
        }, this.loadActivePage)
    }

    // <ActivePageLabel data={this.numToPageID(this.state.activePage)}/>
    render() {
        return (
            <div id="page-wrapper">
                <Menu pages={this.state.pages} updatePage={this.updateActivePage}/>
                {this.loadActivePage()}
            </div>
        );
    }
}

export default App;
