import React, {Component} from 'react';
import './Menu.css'

class Menu extends Component {

    constructor(props) {
        super(props);

        this.state = {
            pages: props.pages
        }
    }

    componentDidMount() {
        this.setButtonFadeTimers();
    }

    setButtonFadeTimers() {
        for (var i = 0; i < this.state.pages.length; i++) {
            var style = this.getStyleForIndex(i);
            document.getElementById(this.state.pages[i] + "-menu-opt").style = style;
        }
    }

    getStyleForIndex(n) {
        return "animation-delay: " + (n - 1) / 5 + "s;";
    }

    setActivePage(e, pageID) {
        this.props.updatePage(pageID);
    }

    render() {
        return (
            <div id="menu-wrapper">
                <table id="menu-table">
                    <tbody>
                    <tr>

                        {
                            this.state.pages.map(page => (
                                <td onClick={e => this.setActivePage(e, page)}
                                    className="menu-opt"
                                    id={page + "-menu-opt"}
                                    key={page + "-menu-opt"}>
                                    <h3 className="menu-text">{page.toUpperCase()}</h3>
                                </td>
                            ))
                        }

                    </tr>
                    </tbody>
                </table>
            </div>
        );
    }

}

export default Menu;
